var app = require("../../common/common.js");
import mtFooter from "../../components/forumfooter.vue";
export default {
	components: {
		mtFooter
	},
	data:function(){
		return {
			pageLoad:false, 
			pageHide:false,
			pageData:{},
		}
		
	},

	onLoad: function (option) {
		uni.showNavigationBarLoading();
		this.getPage();
	},
	onReady: function () {
		uni.setNavigationBarTitle({
			title: "论坛板块",
		});
	},
	onShow: function () {
		if (this.pageHide) {
			this.pageHide = false;
			this.getPage();
		}
	},
	onHide: function () {
		this.pageHide = true;
	},
	onPullDownRefresh:function(){
		this.refresh();
	} ,
	 
	methods: {
		getPage: function () {
			var that = this;
			uni.request({
				url: app.apiHost + "/module.php?m=forum_group&ajax=1&fromapp=" + app.fromapp(),
				success: function (data) {
					that.pageLoad = true;
					that.pageData = data.data.data;
					uni.hideNavigationBarLoading();
				}
			})
		},
		refresh:function(){
			this.getPage();
			setTimeout(function(){
				uni.stopPullDownRefresh();
			},1000)
		},
		goList: function (id) {
			uni.navigateTo({
				url: "../forum/list?gid=" + id
			})
		},	
		goForum: function (id) {
			uni.navigateTo({
				url: "../forum/show?id=" + id
			})
		}
	},
}
